import dayjs from 'dayjs';

export const convertPressure = (value) => {
    return (value * 0.750063755419211).toFixed(0);
};

export const translateWindDirection = (value) => {
    // --  (ENE)
    //   (E)
    // --  (ESE)
    // -  (SE)
    // --  (SSE)
    //   (S)
    // --  (SSW)
    // -  (SW)
    // --  (WSW)
    //   (W)
    // --  (WNW)
    // -  (NW)
    // --  (NNW)
    //   (N)

    const directions = [
        { value: 'ENE', label: '-- ' },
        { value: 'E', label: ' ' },
        { value: 'ESE', label: '-- ' },
        { value: 'SE', label: '- ' },
        { value: 'SSE', label: '-- ' },
        { value: 'S', label: ' ' },
        { value: 'SSW', label: '-- ' },
        { value: 'SW', label: '- ' },
        { value: 'WSW', label: '-- ' },
        { value: 'W', label: ' ' },
        { value: 'WNW', label: '-- ' },
        { value: 'NW', label: '- ' },
        { value: 'NNW', label: '-- ' },
        { value: 'N', label: ' ' }
    ];

    return directions.find((dir) => dir.value === value).label;
};

export const getValueDay = (date) => {
    const daysOfWeek = ['', '', '', '', '', '', ''];

    if (dayjs().format('YYYY-MM-DD') === date) {
        return '';
    } else if (dayjs().add(1, 'day').format('YYYY-MM-DD') === date) {
        return '';
    } else {
        let numberDayWeek = dayjs(date).day() - 1; //-1   
        return daysOfWeek[numberDayWeek];
    }
};

export const formatDate = (date, format) => {
    return dayjs(date).format(format);
};
